* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 14px;
  line-height: 1.2;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.header {
  height: 60px;
  background-color: #390ea6;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  padding: 0;
  position: relative;
}

.search-form input {
  height: 30px;
  width: 250px;
  border-radius: 4px;
  outline: none;
  padding-left: 5px;
  padding-right: 80px;
}

.search-form input:focus {
  border-color: #dfd8f3;
}

.btn {
  height: 24px;
  cursor: pointer;
  border-radius: 4px;
}

.search-form button {
  position: absolute;
  top: 3px;
  right: 3px;
}

.btn:focus, .btn:hover {
  border-color: #85aab5;
  box-shadow: 0 0 2px #390ea6;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 15px);
  border-color: #908b8b;
  box-shadow: 2px 4px 4px -2px #160444;
}

.photo-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  object-position: center;
  display: block;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  text-align: center;
  margin: 5px;
}

.load-more {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.is-hidden {
  display: none;
}

